import React, { useEffect, useRef, useState } from 'react';
import Slider from 'react-slick';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';
import { styled } from 'styled-components';
import axios from 'axios';
import './CaroselTest.scss';

const CaroselTest = () => {
  //   const settings = {
  //     dots: true, //   
  //     infinite: true, //  
  //     speed: 500, //    
  //     slidesToShow: 3, //    
  //     slidesToScroll: 1, //     
  //     centerMode: true, //    
  //     centerPadding: '10px', //   padding 
  //     autoplay: true, //  
  //     autoplaySpeed: 2000, //   
  //     draggable: false, // 
  //     fade: false, //   
  //     arrows: true, // , 
  //     vertical: false, //  
  //     initialSlide: 1, //   
  //     pauseOnFocus: true, // focus 
  //     pauseOnHover: true, // hover 
  //     responsive: [
  //       //  
  //       {
  //         breakpoint: 480, // ()px  
  //         settings: {
  //           slidesToShow: 1,
  //           arrows: true,
  //         },
  //       },
  //     ],
  //   };

  const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    arrows: true,
    centerMode: true,
    centerPadding: '0px',
  };

  const ImageLayout = styled('div')`
    display: flex;
    justify-content: center;
    align-items: center;
  `;

  const LayoutDiv = styled('div')`
    background-color: black;
    display: flex;
    justify-content: center;
    align-items: center;
  `;

  const [imageList, setImageList] = useState([]);

  useEffect(() => {
    axios
      .request({
        method: 'get',
        url: '/photo/testedBySelectPhoto',
      })
      .then((resp) => {
        setImageList(resp.data);
      })
      .catch((error) => {
        console.log(error);
      });
  }, []);

  return (
    <LayoutDiv>
      <Slider className="custom-slider" {...settings}>
        {imageList.map((image) => {
          return (
            <ImageLayout>
              <img
                style={{ width: '100%', objectFit: 'contain' }}
                src={`/images/${image.sysName}`}
              ></img>
            </ImageLayout>
          );
        })}
      </Slider>
    </LayoutDiv>
  );
};

export default CaroselTest;
