import React, { useEffect, useRef, useState } from 'react';
import Slider from 'react-slick';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';
import { styled } from 'styled-components';
import axios from 'axios';
import './CaroselImage.module.scss';

const CaroselImage = ({ imgBase64 }) => {
  //   const settings = {
  //     dots: true, //   
  //     infinite: true, //  
  //     speed: 500, //    
  //     slidesToShow: 3, //    
  //     slidesToScroll: 1, //     
  //     centerMode: true, //    
  //     centerPadding: '10px', //   padding 
  //     autoplay: true, //  
  //     autoplaySpeed: 2000, //   
  //     draggable: false, // 
  //     fade: false, //   
  //     arrows: true, // , 
  //     vertical: false, //  
  //     initialSlide: 1, //   
  //     pauseOnFocus: true, // focus 
  //     pauseOnHover: true, // hover 
  //     responsive: [
  //       //  
  //       {
  //         breakpoint: 480, // ()px  
  //         settings: {
  //           slidesToShow: 1,
  //           arrows: true,
  //         },
  //       },
  //     ],
  //   };

  const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    arrows: true,
    centerMode: true,
    centerPadding: '0px',
  };

  const ImageLayout = styled('div')`
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  `;

  const LayoutDiv = styled('div')`
    display: flex;
    justify-content: center;
    align-items: center;
  `;

  return (
    <LayoutDiv>
      <Slider
        {...settings}
        style={{
          width: '400px',
          height: '500px',
          alignIten: 'center',
        }}
      >
        {imgBase64.map((item) => {
          return (
            <div>
              <ImageLayout>
                <img
                  src={item}
                  style={{
                    height: '100%',
                    width: '80%',
                    objectFit: 'contain',
                    float: 'left',
                  }}
                />
              </ImageLayout>
            </div>
          );
        })}
      </Slider>
    </LayoutDiv>
  );
};

export default CaroselImage;
