import axios from 'axios';
import React, { createContext, useContext, useEffect, useState } from 'react';
import { Provider } from 'react-redux';

const FeedPhotoUpload = ({ uploadState, setUploadState }) => {
  const [feedPostId, setFeedPostId] = useState(0);
  const [files, setFiles] = useState([]);
  //    
  const [feedList, setFeedList] = useState([]);

  useEffect(() => {
    axios({
      url: '/feedpost/selectfeedlist/',
      method: 'GET',
    })
      .then((resp) => {
        setFeedList(resp.data);
      })
      .catch((error) => {
        console.log(error);
      });
  }, []);

  const handleChange = (e) => {
    setFeedPostId(e.target.value);
  };

  const handleFileChange = (e) => {
    // const selectedFile = e.target.files[0];
    // setFile(selectedFile);
    //    
    const selectedFiles = Array.from(e.target.files);
    setFiles(selectedFiles);
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    if (feedPostId === Number(e.target.value)) {
      alert('0   .');
      return;
    }

    const formData = new FormData();
    formData.append('feedPostId', feedPostId);

    // formData.append('file', file);
    //    
    files.forEach((file) => {
      formData.append('files', file);
    });

    axios
      .request({
        method: 'post',
        url: '/photo/insertPhoto',
        headers: {
          'Content-Type': 'multipart/form-data',
        },
        data: formData,
      })
      .then((resp) => {
        setUploadState(
          '  : ' + feedPostId + ' : ' + files[0].name + '  ',
        );
        setFiles([]);
      })
      .catch((error) => console.log(error));
  };

  return (
    <div>
         
      <form onSubmit={handleSubmit}>
        <p>
          <input
            type="file"
            multiple
            onChange={handleFileChange}
            accept="image/*"
          />
          <input
            type="text"
            placeholder=" ."
            value={feedPostId}
            onChange={handleChange}
          />
          <button type="submit"></button>
        </p>
      </form>
      <p>
          :
        {feedList.map((e) => {
          return `${e.feedPostId} / `;
        })}
      </p>
    </div>
  );
};

const UserProfileUpload = ({ uploadState, setUploadState }) => {
  const [userNo, setUserNo] = useState(0);
  const [file, setFile] = useState([]);
  //    
  const [userList, setUserList] = useState([]);

  //   
  useEffect(() => {
    axios({
      url: '/auth/selectUserList',
      type: 'GET',
    })
      .then((resp) => {
        setUserList(resp.data);
      })
      .catch((error) => {
        console.log(error);
      });
  }, []);

  const handleUserChange = (e) => {
    setUserNo(e.target.value);
  };

  const handleFileChange = (e) => {
    setFile(e.target.files[0]);
    //    
  };

  //  
  const handleSubmit = (e) => {
    e.preventDefault();
    if (userNo === Number(e.target.value)) {
      alert('0   .');
      return;
    }

    const formData = new FormData();
    formData.append('userNo', userNo);
    formData.append('files', file);
    //    

    axios({
      method: 'post',
      url: '/photo/insertPhoto',
      headers: {
        'Content-Type': 'multipart/form-data',
      },
      data: formData,
    })
      .then((resp) => {
        setUploadState(
          '  : ' + userNo + ' : ' + file.name + '  ',
        );
        setFile([]);
      })
      .catch((error) => console.log(error));
  };

  return (
    <div>
         
      <form onSubmit={handleSubmit}>
        <p>
          <input type="file" onChange={handleFileChange} accept="image/*" />
          <input
            type="text"
            placeholder=" ."
            value={userNo}
            onChange={handleUserChange}
          />
          <button type="submit"></button>
        </p>
      </form>
      <p>
          :
        {userList.map((e) => {
          return `${e.userNo} / `;
        })}
      </p>
    </div>
  );
};

const ImageUpload = () => {
  const [uploadState, setUploadState] =
    useState('   ');
  return (
    <div>
      <FeedPhotoUpload {...{ uploadState, setUploadState }}></FeedPhotoUpload>
      <br />
      <hr />
      <br />
      <UserProfileUpload
        {...{ uploadState, setUploadState }}
      ></UserProfileUpload>
      <br />
      <hr />
      <br />
      <p>{uploadState}</p>
    </div>
  );
};

export default ImageUpload;
