import axios from 'axios';
import React, { useCallback, useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { styled } from 'styled-components';
import ChatBox from './Component/ChatBox';
import SearchMember from './Component/SearchMember';
import ListElement from './Component/ListElement';
import { Stomp } from '@stomp/stompjs';
import SockJS from 'sockjs-client';
import { setDMNONE } from '../../modules/Redux/DMSetting';

function DMList() {
  const accessToken = useSelector((state) => state.member.access);
  const loginUserNo = useSelector((state) => state.member.userNo);
  const settingChatUserNo = useSelector((state) => state.dmSetting.userNo); //    userNo
  const settingIsUserAlert = useSelector(
    (state) => state.dmSetting.isUserAlert,
  );
  const [DMRoomList, setDMRoomList] = useState([]); //   
  const [DMRoom, setDMRoom] = useState({}); //   
  const [DMList, setDMList] = useState([]); //    
  const [CheckList, setCheckList] = useState([]); //   

  const [stompClient, setStompClient] = useState();

  const dispatch = useDispatch();
  const onSetDMNONE = useCallback(() => dispatch(setDMNONE(), [dispatch]));

  var headers = {
    login: '1',
    passcode: '1',
    accessToken: accessToken,
    loginUserNo: loginUserNo,
    roomId: DMRoom.roomId,
    settingChatUserNo: settingChatUserNo,
  };

  // DMList       STOMP  
  useEffect(() => {
    const socketUrl = `http://localhost:9999/ws`;
    const socket = new SockJS(socketUrl);
    const client = Stomp.over(socket);
    client.heartbeat.outgoing = 10000;
    client.heartbeat.incoming = 10000;

    setStompClient(client);
    console.log(' ');

    client.connect({}, () => {
      console.log('STOMP  ');
    });
    return () => {
      if (stompClient) {
        stompClient.disconnect();
      }
    };
  }, []);

  // stompClient  DMRoom roomId      roomId   
  // stompClient  DMRoom roomId        
  // ListElement       .
  useEffect(() => {
    if (stompClient && DMRoom.roomId) {
      const subscription = stompClient.subscribe(
        `/topic/${DMRoom.roomId}`,
        (receivedMessage) => {
          // console.log(receivedMessage.body);
          setDMList((prev) => [...prev, JSON.parse(receivedMessage.body)]);
        },
        headers,
      );

      return () => {
        subscription.unsubscribe();
      };
    }
  }, [stompClient, DMRoom.roomId]);

  // More      
  const disconnect = () => {
    if (stompClient && DMRoom.roomId) {
      axios({
        method: 'delete',
        url: '/DM/deleteUserDMRoomUser',
        headers: {
          Authorization: `Bearer ${accessToken}`,
        },
        params: {
          userNo: loginUserNo,
          roomId: DMRoom.roomId,
        },
      })
        .then(() => {
          onSetDMNONE();
          updateRoomList();
          updateDMList();
        })
        .catch((error) => console.log(error));
    }
  };

  // DM DM 

  useEffect(() => {
    if (accessToken) {
      if (DMList.length > 0) {
        axios({
          method: 'put',
          url: '/DM/updateDMRead',
          headers: {
            Authorization: `Bearer ${accessToken}`,
          },
          params: {
            userNo: loginUserNo,
            roomId: DMRoom.roomId,
            messageId: DMList[DMList.length - 1].messageId,
          },
        }).then(() => {});
      }
    }
  }, [DMList]);

  //     

  useEffect(() => {
    if (settingChatUserNo) {
      if (settingChatUserNo !== loginUserNo) {
        axios({
          method: 'get',
          url: '/DM/createRoom',
          headers: {
            Authorization: `Bearer ${accessToken}`,
          },
          params: {
            loginUserNo: loginUserNo,
            clickuserNo: settingChatUserNo,
          },
        })
          .then((resp) => {
            if (resp.data !== '') {
              setDMRoomList(resp.data);
            } else {
              if (settingIsUserAlert) {
                alert('  .');
              }
            }
          })
          .catch((error) => console.log(error));
      } else {
        alert('    .');
      }
    }
  }, [settingChatUserNo]);

  //sendBtn   send     
  const Send = (message) => {
    const currentTime = new Date().getTime();
    stompClient.send(
      '/app/chat/' + DMRoom.roomId,
      {},
      JSON.stringify({
        userNo: loginUserNo,
        message: message,
        writeDate: currentTime,
      }),
    );
  };

  const SendWithImage = (messageId) => {
    const currentTime = new Date().getTime();
    stompClient.send(
      '/app/chatImage/' + DMRoom.roomId,
      {},
      JSON.stringify({
        userNo: loginUserNo,
        messageId: messageId,
        writeDate: currentTime,
      }),
    );
  };

  //    ,
  const imageSend = (file) => {
    insertPhoto(file);
  };

  const insertPhoto = (file) => {
    const formData = new FormData();
    [...file].forEach((item) => {
      formData.append('files', item);
    });
    axios({
      method: 'POST',
      url: '/photo/insertChatPhoto',
      headers: {
        'Content-Type': 'multipart/form-data',
      },
      data: formData,
    })
      .then((resp) => {
        SendWithImage(resp.data);
      })
      .catch((error) => {
        console.log(error);
      });
  };

  // ListElement(    )
  const handleListElementClick = (room) => {
    setDMRoom(room);
  };

  // UserNo   
  const updateRoomList = () => {
    if (loginUserNo > 0) {
      axios({
        method: 'get',
        url: '/DM/selectChatList',
        headers: {
          Authorization: `Bearer ${accessToken}`,
        },
        params: {
          userNo: loginUserNo,
        },
      })
        .then((resp) => {
          setDMRoomList(resp.data);
        })
        .catch((error) => console.log(error));
    }
  };

  useEffect(() => {
    updateRoomList();
  }, [loginUserNo]);

  //ListElement(  DB  DM )
  const updateDMList = () => {
    if (accessToken) {
      axios
        .request({
          url: '/DM/selectDMbyRoomid',
          method: 'get',
          headers: {
            Authorization: `Bearer ${accessToken}`,
          },
          params: {
            roomId: DMRoom.roomId,
          },
        })
        .then((resp) => {
          setDMList(resp.data);
        })
        .catch((error) => console.log(error));
    }
  };

  useEffect(() => {
    //     
    if (Object.keys(DMRoom).length !== 0) {
      updateDMList();
    }
  }, [DMRoom.roomId]);

  return (
    <DMListOuter>
      <div className="chatBox">
        <ChatBox
          stompClient={stompClient}
          DMList={DMList}
          setDMList={setDMList}
          loginUserNo={loginUserNo}
          DMRoomList={DMRoomList}
          DMRoom={DMRoom}
          Send={Send}
          disconnect={disconnect}
          imageSend={imageSend}
        ></ChatBox>
      </div>
      <div className="List">
        <div className="searchBox">
          <SearchMember></SearchMember>
          {/* <input className='search' type='text'></input> */}
        </div>
        <div className="chatList">
          {DMRoomList.map((dto) => (
            <ListElement
              key={dto.roomId}
              room={dto}
              setDMRoom={handleListElementClick}
            />
          ))}
        </div>
      </div>
    </DMListOuter>
  );
}

const DMListOuter = styled.div`
  padding-top: 10px;
  margin: auto;
  width: 1000px;
  height: 600px;
  display: flex;

  .chatBox {
    height: 100%;
    width: 60%;
    padding: 5px;
  }
  .List {
    height: 100%;
    width: 40%;
    border-radius: 20px;
    padding: 5px;
  }
  .searchBox {
    height: 11%;
    width: 100%;
    border: 1px solid lightgray;
    border-radius: 5px;
    margin-bottom: 10px;
  }
  .chatList {
    height: 90%;
    width: 100%;
    background-color: white;
    border: 1px solid lightgray;
    border-radius: 5px;
    overflow-wrap: break-word;
    overflow: overlay;
    padding: 1rem;
  }
`;

export default DMList;
